import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {
      path: '/albums',
      name: 'albums',
      // route level code-splitting
      // this generates a separate chunk (albums.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "albums" */ './views/Albums.vue')
    },
    {
      path: '/potent-flask',
      name: 'potent-flask',
      // route level code-splitting
      // this generates a separate chunk (albums.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "albums" */ './views/PotentFlask.vue')
    },
    {
      path: '/commissions-vol-I',
      name: 'commissions-vol-I',
      // route level code-splitting
      // this generates a separate chunk (albums.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "albums" */ './views/CommissionsVolI.vue')
    }          
  ]
})
